.ProfileCard {
  text-align: center;
  align-items: center;
  width: 100%;
  margin-top: 3%;
  margin-bottom: 2%;
  box-shadow: 0 1px 1px rgba(50, 50, 50, 0.4);
  border-radius: 8px;
  background: white;
  padding-bottom: 4%;
}

.UserPic {
  box-shadow: 0 2px 2px rgba(50, 50, 50, 0.5);
  right: calc(48%);
  width: 125px;
  height: 125px;
  border: 6px solid #fff;
  border-radius: 50%;
  margin-top: -75px;
  background-size: cover;
  background-color: white;
}

.HeaderAnimation {
  height: 150px;
  background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
  overflow: hidden;
  border-radius: 8px 8px 0 0;
}

.Row {
  display: flex;
  margin-top: 3%;
}

.Column {
  flex: 50%;
  margin-top: -5%;
}

.BasicDetails {
  text-align: left;
  padding-left: 8%;
  font-family: Roboto, Helvetica, Arial, sans-serif;
}

.Name {
  font-weight: bolder;
  font-size: large;
  margin-bottom: 2px;
}

.UserName {
  color: rgba(0, 0, 0, 0.904);
  margin-bottom: 2%;
}

.Email {
  margin-bottom: 3%;
}

.Bio {
  font-size: 15px;
}

.FollowersFollowing {
  display: flex;
}

.Following {
  margin-right: 5%;
  cursor: pointer;
}

.Following p:hover {
  border-bottom: 2px solid rgba(0, 132, 255, 0.596);
}

.Follower {
  margin-right: 0%;
  cursor: pointer;
}

.Follower p:hover {
  border-bottom: 2px solid rgba(0, 132, 255, 0.596);
}

.FollowersFollowing a {
  font-size: 15px;
  color: rgba(0, 0, 0, 0.644);
}

.FollowersFollowing a b {
  color: black;
}

.FollowersFollowing a:hover {
  text-decoration: underline;
  color: #1d94dd;
}

.Reputation {
  margin-right: 5%;
}

.ButtonsSection {
  padding-right: 8%;
  display: flex;
  flex-direction: column;
}

.BigButtonsSection {
  display: flex;
  justify-content: flex-end;
}

.ChatButton {
  width: 20%;
  margin-right: 10px;
}

.EditOrFollowButton {
  width: 35%;
}

.SocialLinks {
  font-size: 30px;
  float: right;
  width: 100%;
  margin-top: 15%;
}

.SocialLinks a {
  float: right;
  margin-left: 10%;
  color: black;
  cursor: pointer;
}

.SocialLinks a:hover {
  color: #128fdd;
}

.LoadingButton {
  text-align: center;
  border-radius: 20px;
  border: 1px solid #1d94dd;
  color: #1d94dd;
  font-weight: bold;
  padding: 5% 10%;
  width: 120px;
  font-size: 16px;
  cursor: pointer;
}

.LoadingButton:hover {
  background-color: rgba(29, 161, 241, 0.2);
}

.RecentPostSection {
  margin-bottom: 2%;
  display: flex;
  justify-content: center;
}

.RecentPostSection p {
  font-size: 17px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.644);
  width: fit-content;
  border-bottom: 2px dotted #1d94dd;
  border-top: 2px dotted #1d94dd;
}

.ModalArea {
  width: 33.333%;
  display: flex;
  justify-content: center;
}

.ResponseLoader{
  text-align: center;
}

